<template>
  <div id="grouplists">
    <Header>
      <el-breadcrumb separator-class="el-icon-arrow-right" slot="left">
        <el-breadcrumb-item>预算管理</el-breadcrumb-item>
        <el-breadcrumb-item>预算项目</el-breadcrumb-item>
      </el-breadcrumb>
      <div slot="right">
      </div>
    </Header>
    <div class="content">
      <el-row :gutter="20">
        <el-col :span="14" class="dept-left">
          <el-row class="title-head">
            <el-col :span="20" class="title-left">
              钉钉部门
            </el-col>
            <el-col :span="4" class="title-right">
              预算项目
            </el-col>
          </el-row>
          <el-tree
            ref="depttrees"
            :data="depttrees"
            show-checkbox
            node-key="deptId"
            :default-expand-all="true"
            :props="deptProps"
            class="depttree">
              <span class="z-tree-node" slot-scope="{ node, data }">
              <span>{{ node.label }}</span>
              <span class="note-right" v-if="data.group">{{data.group.name}}</span>
            </span>
          </el-tree>
        </el-col>
        <el-col :span="10" class="dept-right">
          <el-row class="title-head">
            <el-col :span="20" class="title-left">
              关联预算项目
            </el-col>
          </el-row>
          <el-row :gutter="10" v-for="(group, $index) in grouplists" :key="'grouplists-' + $index" class="grouplists">
            <el-col :span="18">
              {{group.name}}
            </el-col>
            <el-col :span="6" style="text-align:right;">
              <el-button type="primary" size="mini" plain @click="selectGroup(group)">关联</el-button>
            </el-col>
          </el-row>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
  import Header from '../../components/Header'
  export default {
    name: 'grouplists',
    components: {
      Header
    },
    data() {
      return {
        page: 1,
        limit: 10,
        count: 0,
        year: new Date().getFullYear(),
        deptGroups: [],
        depttrees: [],
        deptProps: {
          children: 'children',
          label: 'deptName'
        },
        grouplists: []
      }
    },
    methods: {
      getGroupLists() {
        this.$http.get(`/ec/api/depts/grouplists`).then(res =>{
          let resData = res.data;
          if(resData.errcode === 0) {
            this.grouplists = resData.data || [];
          }
        })
      },
      getDeptTrees() {
        this.$http.get('/ec/api/depts/trees').then(res => {
          let resData = res.data;
          if(resData.errcode === 0) {
            this.depttrees = resData.data || []
          }
        })
      },

      selectGroup(group) {
        let that = this;
        if(!this.$refs.depttrees.getCheckedNodes().length) {
          this.$message({
            message: '请选择需要关联的部门列表',
            type: 'warning'
          })
          return;
        }

        this.$confirm(`确定要关联预算体 “${group.name}”`, '关联确认', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
        }).then(() =>{
          let deptIds = [];
          for(let dept of that.$refs.depttrees.getCheckedNodes()) {
            deptIds.push(dept.deptId)
          }

          this.$http.post('/ec/api/depts/groupmap', {
            deptIds,
            group: group.code
          }).then(res =>{
            let resData = res.data;
            if(resData.errcode === 0) {
              this.$message({
                message: '关联成功',
                type: 'success'
              })
              this.getDeptTrees();
            }
          })
        });
      }
    },
    created() {
      this.page = this.$route.query.page || 1;
      this.getDeptTrees();
      this.getGroupLists()
    }
  }
</script>

